import $ from "../core";

$.prototype.get = async function (url, dataTypeResponse = "json") {
    let result = await fetch(url);

    if (!result.ok)
        throw new Error("Couldn't get " + url + ` status:${result.status}`);
    switch (dataTypeResponse) {
        case "json":
            return result.json();
        case "text" :
            return result.text();
        case "blob":
            return result.blob();
    }
};

$.prototype.post = async function (url, data, headers, dataTypeResponse = "json") {

    let result = await fetch(url, {
        method: "POST",
        headers: headers,
        body: data
    });

    if (!result.ok)
        throw new Error("Couldn't get " + url + ` status:${result.status}`);
    switch (dataTypeResponse) {
        case "json":
            return result.json();
        case "text" :
            return result.text();
        case "blob":
            return result.blob();
    }
}
